<template>
  <div class="container wrapper">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item
        :label="`目标${index + 1}`"
        v-for="(item, index) in form.tasks"
        :key="`task-${index}`"
      >
        <el-input
          v-model="item.name"
          placeholder="目标名称不超过5个字"
          style="width: 200px; margin-right: 20px"
        ></el-input>
        <el-date-picker
          v-model="item.day"
          type="date"
          format="yyyy/MM/dd"
          value-format="yyyy/MM/dd"
          placeholder="选择日期"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <el-button icon="el-icon-plus" circle size="small" v-show="index===0 && form.tasks.length<4" @click="onAddItem(form.tasks.length+1)"></el-button>
      </el-form-item>
      <el-form-item :label="`预览`">
        <mul-days :_msg="msg" :_created="created" />
      </el-form-item>
      <el-form-item label="链接🔗">
        <el-link
          type="primary"
          icon="el-icon-document-copy"
          v-clipboard:copy="`${public_link}?msg=${msg}&created=${created}`"
          v-clipboard:success="handleCopy"
          >复制链接</el-link
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import MulDays from "../../widget/mul-days-matter.vue";
export default {
  components: { MulDays },
  data() {
    return {
      form: {
        tasks: [{ day: "2022/12/31", name: "2022结束" }],
      },
      link: this.$global_path + "/wid/mul-days-matter",
      public_link:
        "https://httishere.github.io/notion-widget/new/mul-days-matter.html",
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now();
        },
      },
      created: null,
    };
  },
  mounted() {
    let cur_date = new Date();
    this.created = `${cur_date.getFullYear()}/${
      cur_date.getMonth() + 1
    }/${cur_date.getDate()}`;
  },
  computed: {
    msg() {
      let tasks = this.form.tasks,
        list = [];
      tasks.forEach((item) => {
        list.push(`${item.name}-${item.day}`);
      });
      return list.join(",");
    },
  },
  methods: {
    handleCopy() {
      this.$message({
        message: "复制成功，快去Notion体验一下吧",
        type: "success",
      });
    },
    onAddItem(index) {
        this.form.tasks.push({
            name: `目标名称${index}`,
            day: "2022/12/31",
        })
    }
  },
};
</script>
<style lang="less" scoped>
iframe {
  width: 320px;
  height: 320px;
  outline: none;
  border: none;
}
</style>
